@import "../../styles/index.scss";

.banner {
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    width: 100%;
    height: 220px;
    border-radius: 25px;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: height 0.8s ease;
    margin-bottom: 50px;

    @media (max-width: 1280px) {
        height: 170px;
    }

    @media (max-width: 768px) {
        height: 110px;
        border-radius: 10px;
    }

    &__overlay {
        position: absolute;
        z-index: 4;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0, 0, 0, 0.4);
        border-radius: 25px;


        @media (max-width: 768px) {
            border-radius: 10px;
        }
    }

    &__image {
        position: relative;
        z-index: 3;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 25px;


        @media (max-width: 768px) {
            border-radius: 10px;
        }
    }

    &__title {
        width: 100%;
        height: auto;
        padding: 20px;
        position: absolute;
        z-index: 4;
        text-align: center;
        font-size: 3rem;
        font-weight: 700;
        color: white;
        @include text-shadow-effect($black, $shadow-color );

        @media (max-width: 1280px) {
            text-shadow: none;
            font-size: 2.5rem;
        }

        @media (max-width: 768px) {
            text-align: start;
            font-size: 1.5rem;

            & span {
                display: flex;
            }
        }

    }
}