@import "../../styles/index.scss";

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    gap: 20px;
    box-sizing: border-box;
}

.sectionCollapse {
    display: flex;
    flex-direction: column;
    width: 100%;
    max-width: 1024px;
    margin: 0 auto;
    gap: 15px;

    @media (max-width: 768px) {
        width: 100%;
        margin: 0px;
    }
}

.collapseButton {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 10px 20px 10px 15px;
    background-color: $color-orange;
    border: none;
    cursor: pointer;
    font-size: 24px;
    font-weight: 700;
    color: white;
    border-radius: 10px;
    box-sizing: border-box;
    letter-spacing: 1px;

    @media (max-width: 768px) {
        font-size: 14px;
    }
}

.icon {
    margin-left: 10px;
    transition: transform 0.3s ease;
}

.rotate {
    transform: rotate(180deg);
}

.content {
    overflow: hidden;
    max-height: 0;
    opacity: 0;
    transition:
        max-height 0.2s ease,
        opacity ease 0.3s;
}

.content.active {
    max-height: 200px;
    opacity: 1;
    transition:
        max-height 0.3s ease,
        opacity ease 0.3s;
}

.contentCollapse {
    background-color: #fff;
    border-radius: 0 0 5px 5px;
    box-sizing: border-box;
    font-weight: 400;
    font-size: 18px;
    position: relative;
    top: -80px;
    opacity: 0;
    padding: 0px 20px 0px 20px;
    transition:
        top ease 0.5s,
        opacity ease 0.3s;

    @media (max-width: 768px) {
        font-size: 14px;
    }
}

.contentCollapse.active {
    display: block;
    top: 0;
    opacity: 1;
    transition:
        top ease 0.5s,
        opacity ease 0.3s;
}

.containerCollapse {
    display: flex;
    flex-direction: row;
    width: 100%;
    max-width: 1024px;
    margin: 0 auto;
    justify-content: space-between;
}
